import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Platform, ActionSheetController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { AddTaskPage } from'../add-task/add-task'


import { Task } from '../../interface/task';
import { File } from '@ionic-native/file';

import * as pdfmake from 'pdfmake/build/pdfmake';
import * as pdfFonts from 'pdfmake/build/vfs_fonts';


/**
 * Generated class for the TaskListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

import{ TaskService } from '../../services/task.service';

@IonicPage()
@Component({
  selector: 'page-task-list',
  templateUrl: 'task-list.html',
})
export class TaskListPage {
  tasks:Task[]=[];
  idTask:string;
  Culminated:any; 
  constructor(
    public platform: Platform,
    public actionSheetCtrl: ActionSheetController,
    public navCtrl: NavController,
    public alertCtrl: AlertController, 
    public navParams: NavParams,
    private taskService:TaskService,

   ) {
    this.showTask();
  }

  showTask(){
    this.taskService.getTaskList().subscribe((data:Task[])=>{
      this.tasks = data;

    })
  }
  getItems(ev: any) {
    // Reset items back to all of the items
    // this.showTask();
    // set val to the value of the searchbar
    const val = ev.target.value;
    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.tasks = this.tasks.filter((task:any) => {
        return (task.Name.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })

    }else{
      this.showTask();
    }
  }


  ionViewDidLoad() {

  }

  deleteTask(id){
    this.taskService.removeTask(id);
  }

  confirmDelete() {
    const alert = this.alertCtrl.create({
      title: 'confirm',
      subTitle:'Do you want to delete this data?',
      buttons: [{
        text: 'NO',
        cssClass: 'method-color',
        handler: () => {
          console.log('no');
        }
      },
      {
        text: 'YES',
        cssClass: 'method-color',
        handler: () => {
          this.deleteTask(this.idTask);
          this.showTask();
  
        }
      }]
    });
    alert.present();

  }

  presentActionSheet() {
    const actionSheet = this.actionSheetCtrl.create({
      
        title: 'Boat',
        cssClass: 'action-sheets-basic-page',
        buttons: [
          {
            text: 'Delete',
            role: 'destructive',
            icon: !this.platform.is('ios') ? 'trash' : null,
            handler: () => {
              this.confirmDelete();
            }
          },
          {
            text: 'Edit',
            icon: !this.platform.is('ios') ? 'create' : null,
            handler: () => {
              this.navCtrl.push( AddTaskPage ,{ 'idTask': this.idTask });
            }
          },
          
          // {
          //   text: 'Favorite',
          //   icon: !this.platform.is('ios') ? 'heart-outline' : null,
          //   handler: () => {
          //     console.log('Favorite clicked');
          //   }
          // },
          {
            text: 'Close',
            role: 'cancel', // will always sort to be on the bottom
            icon: !this.platform.is('ios') ? 'close' : null,
            handler: () => {
              
            }
          }
        ]
      });
      actionSheet.present();
    }

    //
    //        Report con pdfMaker
    //
    makePdf() {
      pdfmake.vfs = pdfFonts.pdfMake.vfs;
      var dD = {
      content: [
        {
          image: 'data:image/png;base64,',
          fit: [100, 100]
          },
               [
               { text: 'ATM', style: 'header' },
               { text: 'TRANSPORTE', style: 'sub_header' },
               { text: 'WEBSITE: ', style: 'url' },
               ],
        'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Confectum ponit legam, perferendis nomine miserum, animi. Moveat nesciunt triari naturam.\n\n',
        {
          text: 'Subheader 1 - using subheader style',
          style: 'subheader'
        },
        {
          style: 'tableExample',
          table: {
              headerRows: 1,
              widths: ['auto', '*'],
              body: [
                [{text: 'Header 1', style: 'tableHeader'}, {text: 'Header 2', style: 'tableHeader'}, {text: 'Header 3', style: 'tableHeader'}],
                  [{text:'task',style :'textClient'},  {text:'task', style : 'textClients'},{text:'task', style : 'textClients'}]
                 
              ]
          },
          layout: {
              hLineWidth: function (i, node) {
                  return (i === 0 || i === node.table.body.length) ? 1 : 1;
              },
              vLineWidth: function (i, node) {
                  return (i === 0 || i === node.table.widths.length) ? 1 : 1;
              },
              hLineColor: function (i, node) {
                  return (i === 0 || i === node.table.body.length) ? 'black' : 'black';
              },
              vLineColor: function (i, node) {
                  return (i === 0 || i === node.table.widths.length) ? 'black' : 'black';
              },
              // hLineStyle: function (i, node) { return {dash: { length: 10, space: 4 }}; },
              // vLineStyle: function (i, node) { return {dash: { length: 10, space: 4 }}; },
              // paddingLeft: function(i, node) { return 4; },
              // paddingRight: function(i, node) { return 4; },
              // paddingTop: function(i, node) { return 2; },
              // paddingBottom: function(i, node) { return 2; },
              // fillColor: function (rowIndex, node, columnIndex) { return null; }
          }
      },
        {
          text: 'Subheader 2 - using subheader style',
          style: 'subheader'
        },
        {
          text: 'It is possible to apply multiple styles, by passing an array. This paragraph uses two styles: quote and small. When multiple styles are provided, they are evaluated in the specified order which is important in case they define the same properties',
          style: ['quote', 'small']
        }
      ],

      styles: {
      header: {
      bold: true,
      fontSize: 20,
      alignment: 'right'
      },
      sub_header: {
      fontSize: 18,
      alignment: 'right'
      },tableHeader: {
        bold: true,
        fontSize: 10,
        color: 'black'
    }      ,
      url: {
      fontSize: 16,
      alignment: 'right'
      }
      },
      pageSize: 'A4',
      pageOrientation: 'portrait'
      };
      pdfmake.createPdf(dD).download();//.open();
      }
}



